<template>
  <div class="edit-news-item">
    <Tabs
      v-model="activeTab"
      :data="tabs"
      @update:model-value="onChangeTab"
    >
      <template #default="{ item }">
        {{ item.title }}
      </template>
    </Tabs>
    <div class="edit-news-item__body">
      <Text variant="p" mb="4px" mt="20px">
        {{ t('labels.title') }}
      </Text>
      <Input
        v-model="form.title"
        hide-shadow
        :placeholder="t('labels.title')"
      />
      <Text variant="p" mb="4px" mt="20px">
        {{ t('labels.description') }}
      </Text>
      <QuillEditor
        v-model="form.body"
        min-height="193px"
      />
      <div class="d-flex">
        <Button
          type="primary"
          class="edit-news-item__action"
          @click="onSave"
        >
          {{ t('common.saveChanges') }}
        </Button>
        <Button
          type="default"
          class="edit-news-item__action"
          @click="onDraft"
        >
          {{ t('common.saveDraft') }}
        </Button>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, reactive, ref } from 'vue'
import { useI18n } from '@/shared/plugins/i18n'
import { useActions } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import Tabs from '@/shared/components/UIKit/Tabs'
import Text from '@/shared/components/UIKit/Text/Text'
import Input from '@/shared/components/UIKit/Input'
import Button from '@/shared/components/UIKit/Button'
import QuillEditor from '@/admin/components/Editor/QuillEditor'
// import Quill from '@/components/Editor/Quill'

export default {
  name: 'EditNewsItem',
  components: {
    QuillEditor,
    // Quill,
    Tabs,
    Text,
    Input,
    Button
  },
  emits: ['edit'],
  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const {
      editHistoryRow
    } = useActions(MODULES_NAMES.NEWS)

    const form = reactive({
      ...props.item,
      body: props.item?.body?.draft || props.item?.body?.value
    })

    const tabs = computed(() => [
      { title: t('languages.en'), key: 'en', icon: 'uk' },
      { title: t('languages.ru'), key: 'ru', icon: 'ru' }
    ])
    const activeTab = ref(tabs.value[0])

    const onChangeTab = (tab) => {
      activeTab.value = tab
    }

    const onSave = () => {
      const row = {
        ...form,
        body: {
          value: form.body,
          draft: ''
        }
      }
      editHistoryRow(row)
      emit('edit', row)
    }
    const onDraft = () => {
      const row = {
        ...form,
        body: {
          value: props.item?.body?.value,
          draft: form.body
        }
      }
      editHistoryRow(row)
      emit('edit', row)
    }

    return {
      t,
      tabs,
      form,
      activeTab,
      onSave,
      onDraft,
      onChangeTab
    }
  }
}
</script>

