import { constantCase, pascaleCase } from '@/shared/utils/caseTransforms'

export const paginationCrud = ({
  tableName = 'table',
  data = [],
  columns = [],
  page = 1,
  perPage = 20,
  totalCount = 0,
  filters = {}
}) => {
  return {
    state: {
      [tableName]: {
        data,
        columns,
        page,
        perPage,
        totalCount,
        filters
      }
    },
    mutations: {
      [`SET_${constantCase(tableName)}_DATA`] (state, payload) {
        state[tableName].data = payload
      },
      [`SET_${constantCase(tableName)}_COLUMNS`] (state, payload) {
        state[tableName].columns = payload
      },
      [`SET_${constantCase(tableName)}_PER_PAGE`] (state, payload) {
        state[tableName].perPage = payload
      },
      [`SET_${constantCase(tableName)}_PAGE`] (state, payload) {
        state[tableName].page = payload
      },
      [`SET_${constantCase(tableName)}_TOTAL_COUNT`] (state, payload) {
        state[tableName].totalCount = payload
      },
      [`SET_${constantCase(tableName)}_FILTERS`] (state, payload) {
        state[tableName].filters = payload
      }
    },
    actions: {

    },
    getters: {
      [`get${pascaleCase(tableName)}`] (state) {
        return state[tableName]
      },
      [`get${pascaleCase(tableName)}Data`] (state) {
        return state[tableName].data
      },
      [`get${pascaleCase(tableName)}Columns`] (state) {
        return state[tableName].columns
      },
      [`get${pascaleCase(tableName)}PerPage`] (state) {
        return state[tableName].perPage
      },
      [`get${pascaleCase(tableName)}Page`] (state) {
        return state[tableName].page
      },
      [`get${pascaleCase(tableName)}TotalCount`] (state) {
        return state[tableName].totalCount
      },
      [`get${pascaleCase(tableName)}Filters`] (state) {
        return state[tableName].filters
      },
      [`get${pascaleCase(tableName)}ActiveColumns`] (state) {
        return state[tableName].columns.filter(item => item.isShow)
      }
    }
  }
}
