<template>
  <span class="html" v-html="parsedHtml" />
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'Html',
  props: {
    html: {
      type: String,
      default: ''
    }
  },
  setup (props) {
    const parsedHtml = computed(() => {
      const el = document.createElement('span')
      el.innerHTML = props.html
      return el.innerText
    })

    return {
      parsedHtml
    }
  }
}
</script>

