import Quill from 'quill'
import { quillDefaultOptions } from '@/shared/plugins/quill/quill-options'
import QuillEditor from '@/admin/components/Editor/QuillEditor'

const install = (Vue, globalOptions) => {
  if (globalOptions) {
    QuillEditor.props.globalOptions.default = () => globalOptions
  }
  Vue.component(QuillEditor.name, QuillEditor)
}

Quill.prototype.getHtml = function () {
  return this.container.querySelector('.ql-editor').innerHTML
}

export default { Quill, QuillEditor, install }

export { Quill, QuillEditor, install, quillDefaultOptions }
