import { crudStore } from '@/shared/plugins/vuex'
import { paginationCrud } from '@/admin/store/cruds/table'
import { columns, data } from '@/admin/store/mock/newsMock'

export default crudStore([
  paginationCrud({
    totalCount: 123
  })
],
{
  namespaced: true,
  mutations: {
    EDIT_HISTORY_TABLE_ROW (state, payload) {
      const index = state.table.data
        .findIndex(el => el.id === payload.id)
      if (index === -1) {
        return
      }
      state.table.data.splice(index, 1, payload)
    }
  },
  actions: {
    setMockHistoryData ({ commit }) {
      commit('SET_TABLE_DATA', data)
      commit('SET_TABLE_COLUMNS', columns)
    },
    editHistoryRow ({ commit }, payload) {
      commit('EDIT_HISTORY_TABLE_ROW', payload)
    }
  }
})
