<template>
  <div class="news-grid">
    <div class="news-grid__filters">
      <Input
        hide-shadow
        v-model="filters.search"
        :placeholder="t('common.search')"
        class="news-grid__filters__search"
      >
        <template #prepend>
          <Icon name="search" opacity="0.33" />
        </template>
      </Input>
      <DateRange
        v-model="filters.date"
        :placeholder-to="t('common.to')"
        :placeholder-from="t('common.from')"
        class="news-grid__filters__date-range"
      />
      <SelectInput
        muted
        :placeholder="t('common.crypto')"
        icon="chevron-down"
        class="news-grid__filters__hidden"
      />
    </div>
    <Table :data="data" :columns="columns" class="news-grid__table">
      <template #column="{ column }">
        {{ t(column.title) }}
      </template>
      <template #cell="{ item, column, i }">
        <span
          v-if="column.type === 'button'"
          class="news-grid__table__manage"
          @click="onExpand(i)"
        >
          Manage
        </span>
        <div v-if="column.html" class="news-grid__table__body">
          <Html :html="item.value" class="overflow-ellipsis"/>
          <Badge
            v-if="item.draft"
            type="default"
          >DRAFT</Badge>
        </div>
        <Date
          v-else-if="column.type === 'date'"
          format="DD MMM YYYY HH:mm"
          :date="item"
        />
        <span v-else>{{ item }}</span>
      </template>
      <template #expand="{ item }">
        <EditNewsItem :item="item" @edit="onExpand()"/>
      </template>
    </Table>
    <Pagination
      v-model="page"
      :total-count="totalCount"
      :per-page="perPage"
      class="news-grid__pagination"
    />
  </div>
</template>

<script>
import { computed, reactive, ref } from 'vue'
import { useGetters, useMutations } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import { useI18n } from '@/shared/plugins/i18n'
import { useBus } from '@/shared/plugins/event/useBus'
import events from '@/shared/common/events'
import Table from '@/admin/components/Table/Table'
import Input from '@/shared/components/UIKit/Input'
import Icon from '@/shared/components/Icon/Icon'
import DateRange from '@/shared/components/UIKit/Date/DateRange'
import SelectInput from '@/shared/components/UIKit/Select/SelectInput'
import Pagination from '@/shared/components/UIKit/Pagination'
import Date from '@/shared/components/UIKit/Date/Date'
import EditNewsItem from '@/admin/views/News/EditNewsItem'
import Html from '@/shared/components/UIKit/Text/Html'
import Badge from '@/shared/components/UIKit/Badge'

export default {
  name: 'NewsGrid',
  components: { Badge, Html, EditNewsItem, Date, Pagination, SelectInput, DateRange, Icon, Input, Table },
  setup () {
    const emitter = useBus()
    const { t } = useI18n()
    const {
      getTableColumns: columns,
      getTableData: data,
      getTablePerPage: perPage,
      getTableTotalCount: totalCount,
      getTablePage
    } = useGetters(MODULES_NAMES.NEWS)
    const {
      SET_TABLE_PAGE: setPage
    } = useMutations(MODULES_NAMES.NEWS)

    const expandedIndex = ref(-1)

    const page = computed({
      get: () => getTablePage.value,
      set: setPage
    })
    const filters = reactive({
      date: {
      },
      search: ''
    })

    const onExpand = (i = -1) => {
      if (i !== -1) {
        expandedIndex.value = i
      }
      emitter.emit(events.TABLE_EXPAND, expandedIndex.value)
    }

    return {
      t,
      columns,
      data,
      filters,
      page,
      perPage,
      totalCount,
      onExpand
    }
  }
}
</script>

<style lang="scss">
.news-grid {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding-bottom: 60px;

  &__filters {
    display: flex;
    width: 100%;
    height: 44px;
    margin-bottom: 20px;

    &__search {
      flex-grow: 1;
      min-width: 647px;
    }
    &__date-range {
      margin: 0 20px;
    }
  }
  &__pagination {
    position: absolute;
    bottom: 0;
    width: 100%;
    margin-bottom: 20px;
    margin-top: 50px;
  }
  &__table {
    flex-grow: 1;

    &__manage {
      display: inline;
      padding: 6px 12px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      box-sizing: border-box;
      border-radius: 4px;
      background-color: map-get($colors, "white");

      font-size: 14px;
      cursor: pointer;
    }
    &__body {
      position: relative;

      .html {
        max-width: 113px;
      }
      .badge {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
      }
    }
  }
}
</style>
