<template>
  <div
    ref="el"
    class="quill-editor"
    :style="{
      '--quill-min-height':  minHeight
    }"
  >
    <slot name="toolbar"></slot>
    <div ref="editor"></div>
  </div>
</template>

<script>
import {
  ref,
  watch,
  reactive,
  onMounted,
  onBeforeUnmount
} from 'vue'
import { quillDefaultOptions, Quill } from '@/shared/plugins/quill'

import 'quill/dist/quill.core.css' // import styles
import 'quill/dist/quill.snow.css' // for snow theme
import 'quill/dist/quill.bubble.css' // for bubble theme

export default {
  name: 'QuillEditor',
  emits: ['update:modelValue', 'change', 'focus', 'blur', 'ready'],
  props: {
    content: {
      type: String,
      default: ''
    },
    modelValue: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    options: {
      type: Object,
      required: false,
      default: () => ({})
    },
    globalOptions: {
      type: Object,
      required: false,
      default: () => ({})
    },
    minHeight: {
      type: String,
      default: 'unset'
    }
  },
  setup (props, { emit }) {
    const _options = ref({})
    const _content = ref('')

    const $el = ref()
    const editor = ref()
    const quill = ref()

    const defaultOptions = reactive(quillDefaultOptions)

    const setHtml = (value) => {
      _content.value = value
      quill.value.container.firstChild.innerHTML = value
    }
    const enableEditor = (flag) => {
      quill.value.enable(flag)
    }
    const textChange = () => {
      const html = quill.value.getHtml()
      const text = quill.value.getText()
      // if (html === '<p><br></p>') html = ''
      _content.value = html
      emit('update:modelValue', _content.value)
      emit('change', { html, text, quill: quill.value })
    }
    const selectionChange = range => {
      if (!range) {
        emit('blur', quill.value)
      } else {
        emit('focus', quill.value)
      }
    }
    const initialize = () => {
      if ($el.value) {
        // Options
        _options.value = {
          ...defaultOptions,
          ...props.globalOptions,
          ...props.options
        }
        // Instance
        quill.value = new Quill(editor.value, _options.value)

        enableEditor(false)
        // Set editor content
        if (props.modelValue || props.content) {
          setHtml(props.modelValue || props.content)
        }
        // Disabled editor
        if (!props.disabled) {
          enableEditor(true)
        }
        // Mark model as touched if editor lost focus
        quill.value.on('selection-change', selectionChange)
        // Update model if text changes
        quill.value.on('text-change', textChange)
        // Emit ready event
        emit('ready', quill.value)
      }
    }

    watch(() => props.content, newVal => {
      if (quill.value) {
        if (newVal && newVal !== _content.value) {
          setHtml(newVal)
        } else if (!newVal) {
          setHtml('')
        }
      }
    })

    // Watch content change
    watch(() => props.modelValue, newVal => {
      if (quill.value) {
        if (newVal && newVal !== _content.value) {
          setHtml(newVal)
        } else if (!newVal) {
          quill.value.setText('')
        }
      }
    })

    // Watch disabled change
    watch(() => props.disabled, newVal => {
      if (quill.value) {
        quill.value.enable(!newVal)
      }
    })

    onMounted(() => {
      initialize()
    })
    onBeforeUnmount(() => {
      quill.value = null
    })

    return {
      el: $el,
      editor
    }
  }
}
</script>

<style lang="scss" src="./quill.scss"></style>
